import React from 'react'
import './footer.css'

export const Footer = () => {
    let footerStyle = {           // css but remember semi columns aren't allowed
        position: "relative",
        marginTop: "10vh",
        width: "100%",
        border: "5px solid red"
    }
    return (
        <footer className="bg-dark text-light py-2" style={footerStyle}>
            <p className="text-center">
            Copyright &copy; Code with harry 
            </p>
        </footer>
    )
}
