import "./App.css";
import Header from "./MyWork/Header";
import {Footer} from "./MyWork/Footer";
import {Todos} from "./MyWork/Todos";
import {AddTodo} from "./MyWork/AddTodo";
import {About} from "./MyWork/About";
import React, { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";




function App() {
  let initTodo;
  if(  localStorage.getItem("todos")=== null){
    initTodo = [];
  }else {
    initTodo = JSON.parse( localStorage.getItem("todos"));
  }
  const onDelete = (todo) => {
    console.log(`You clicked on delete`, todo);
    // let index =todos.indexOf(todo);         this is js dom but it won't work in react so you need to import.!                 
    // todos.splice(index, 1);

    setTodos(todos.filter((e) =>{
      return e !== todo;
    }));
    localStorage.setItem("todos", JSON.stringify(todos));

  }

  const addTodo = (title, desc) =>{
    let sno;
    if (todos.length===0){
      sno = 0;
    }else {
       sno = todos[todos.length-1].sno +1;
    }
    // console.log("Hey I'm gonna add items... ok?/", title, desc);
    const myTodo ={
      sno: sno,
      title: title, 
      desc: desc,
    }
    setTodos([...todos, myTodo]);
    console.log(myTodo);
  }


  const [todos, setTodos] = useState(initTodo);
  useEffect(() => {                                                                          // local storage to store data
    localStorage.setItem("todos", JSON.stringify(todos));
  }, [todos]) 

  return (
    <>
    <Router>
    <Header title= "TO DO List" searchBar={false}/>
    <Switch>
          <Route exact path="/" render={()=>{                                                // this is for single page application        
            return (
            <>
                <AddTodo addTodo={addTodo}/>
                <Todos todos={todos} onDelete={onDelete}/>             
            </>)                                                            
          }}>
          </Route>                                               
          <Route exact path="/About">
            <About />
          </Route>
    </Switch>


    <Footer/>
    </Router>
    </>
  );
}

export default App;
