import React from 'react'                // press rfc to display main component inthis file
import PropTypes from 'prop-types'     // press impt for this to display- prototype..
import {Link} from "react-router-dom";

export default function Header(props) {
    return (                                    // its the bootsrap dear
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            {props.title}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link active" aria-current="page" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/about">
                  About
                </Link>
              </li>
            </ul>
           {props.searchBar? <form className="d-flex">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-outline-success" type="submit">
                Search
              </button>
            </form>:" "}
          </div>
        </div>
      </nav>
    )
}
Header.defaultProps = {      // by defaults                // this is for if user is passing the title,  so in app.js remove title="" ok...!!!
  title: "Your Title Here",                 // semi-column ; is not allowed
  searchBar: true
}

Header.propTypes = {
  title: PropTypes.string,
  searchBar: PropTypes.bool.isRequired                // bool cz searchBar is in Boolean type {true / False }
}
